import React from 'react';
import '../App.css';




class Header extends React.Component {

    scrollTo = () => {
        const about = document.getElementById('about');
        window.scrollTo({top: about.getBoundingClientRect().top, behavior: 'smooth'})
    }

    render() {
        return (
            <header id="home" className="App-header">
                <div >
                    <div style={{display: 'flex', flexDirection: 'row',}}>
                        <h1 className="animated wobble delay-1s">Hello,</h1>
                        <div className="animated fadeIn delay-1s" style={{marginLeft: '10px', display: 'flex', flexDirection: 'row'}}> 
                            <h1>I'm</h1>
                            <span style={{marginLeft: '8px', color: '#6853f6', display: 'flex', }}>
                                <h1>Sayo Williams.</h1>
                            </span>
                        </div>
                    </div>
                
                    <div className="animated fadeIn delay-2s slow">
                        <h1>
                            I build things with _code.
                        </h1>
                    </div>

                </div>

                <div className="animated fadeIn delay-2s slower" onClick={this.scrollTo} style={{marginTop: '40px'}}>
                    <button className="header-button">View my work</button>
                </div>
            
            </header>
        );
    }
}

export { Header };

